
import React from "react";
import axios from "axios";
class FormContact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mailSent: false,
      error: null,

    };
  }

  handleFormSubmit = e => {
    e.preventDefault();

    var bodyFormData = new FormData();
    
    bodyFormData.append('title', this.state.title);
    bodyFormData.append('name', this.state.name);
    bodyFormData.append('phone', this.state.phone);
    bodyFormData.append('email', this.state.email);
    bodyFormData.append('notes', this.state.notes);
    axios({
      method: "post",
      url: 'https://forms.alphaland.com.vn/form_contact.php',
      headers: { "Content-Type": "application/json" },
      data: bodyFormData
    })
      .then(response => {
        console.log(response);
        if (this.state.name && this.state.phone !== '') {
          console.log('Cảm ơn quý khách, chúng tôi sẽ liên hệ với quý khách ngay khi có thể');
          this.setState({ mailSent: true });
          window.location.href = '/thanks';
        }
        else {
          console.log('Chưa nhập tên hoặc số điện thoại');
          this.setState({ error: true });
        }
      }).catch(error => {
        console.err(error)
      })
  };

  //handle changing fields

  handleChange = (e, field) => {
    let value = e.target.value;
    let updateValue = {};
    updateValue[field] = value;
    this.setState(updateValue);
  };

  render() {
    const note = 'Xin chào Alphaland! Tôi có câu hỏi cần giải đáp... ';
    return (
      <div className="form-contact">
        <h3>Liên hệ tư vấn</h3>
        <form action="#">
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="Họ và tên *"
              value={this.state.name}
              onChange={e => this.handleChange(e, 'name')}
              required
            />
          </div>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="Số điện thoại *"
              value={this.state.phone}
              onChange={e => this.handleChange(e, 'phone')}
              required
            />
          </div>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="Địa chỉ email"
              value={this.state.email}
              onChange={e => this.handleChange(e, 'email')}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              placeholder="Nhập tiêu đề"
              className="form-control"
              value={this.state.title}
              onChange={e => this.handleChange(e, 'title')}
            />
          </div>
          <div className="form-group">
            <textarea
              className="form-control"
              placeholder={note}
              value={this.state.notes}
              onChange={e => this.handleChange(e, 'notes')}
              rows="5"
            />
          </div>

          <input type="submit" onClick={e => this.handleFormSubmit(e)} value="Gửi" />

          <div>
            {this.state.mailSent && <div className="alert alert-success">Cảm ơn quý khách, chúng tôi sẽ liên hệ với quý khách ngay khi có thể</div>}
            {this.state.error && <div className="alert alert-danger">Chưa nhập tên hoặc số điện thoại</div>}
          </div>
        </form>
      </div>
    );
  }
}

export default FormContact;