import React from "react"

import Layout from "../../components/Layout"
import SEO from "../../components/SEO"
import AllLayouts from "../AllLayouts"


const Page = ({ pageContext }) => {
  const {
    page: { title, pageBuilder, pageId },
  } = pageContext

  const layouts = pageBuilder.layouts || []


  return (
    <Layout>
      <SEO
        title={title}
        description={title + " Bất động sản Alphaland"}
        keywords={title + " Bất động sản Alphaland, bất động sản HCM, dự án bất động sản HCM"}
      />
      {(() => {
        if (pageId === 15) {
          return (
            <></>
          )
        } else {
          return (
            <div className="big-title mb-5">
              <div className="container text-center">
                <h1> {title} </h1>
              </div>
            </div>

          )
        }
      })()}

      {
        layouts.map((layout, index) => {
          return <AllLayouts key={index} componentType={layout.fieldGroupName} componentData={layout} />
        })
      }

    </Layout>
  )
}

export default Page
